@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css?family=Baloo+Bhai&display=swap);
@import url(https://fonts.googleapis.com/css?family=Baloo+Bhai&display=swap);
@import url(https://fonts.googleapis.com/css?family=Baloo+Bhai&display=swap);
.header-container {
  background: #ececec;
  display: flex;
  justify-content: center;
}

.header-title {
  font-size: 70px;
  font-family: "Pacifico", cursive;
}

@media screen and (min-width: 690px) {
  .header-title {
    font-size: 40px;
  }

  .header-container {
    align-content: center;
    margin: 0 40px;
    padding-left: 50px;
  }
}

.input-container {
  display: flex;
  margin: 0 0 10px 0;
}

.button-search {
  font-size: 26px;
  font-family: "Baloo Bhai", cursive;
  background: #f78888;
}

.input-search {
  font-size: 26px;
  flex-grow: 100;
  background: #f78888;
  font-family: "Baloo Bhai", cursive;
  text-align: center;
}

@media screen and (min-width: 690px) {
  .input-search {
    font-size: 26px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    margin-right: 0;
    border: none;
  }
  .button-search {
    font-size: 20px;
    border: none;
    margin: 0;
    background: purple;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
  }
}

.books-container-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 260px;
}

.books-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.book-container {
  display: flex;
  width: 90%;
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(144, 204, 244, 0.7)),
    color-stop(rgba(144, 204, 244, 0.7)),
    color-stop(rgba(144, 204, 244, 0.7)),
    to(#fff49c)
  );
  background: linear-gradient(
    rgba(144, 204, 244, 0.7),
    rgba(144, 204, 244, 0.7),
    rgba(144, 204, 244, 0.7),
    #fff49c
  );
  margin-bottom: 10px;

  font-family: "Baloo Bhai", cursive;
  border-radius: 10px;
}

.book-img {
  max-width: 200px;
  border: 2px solid black;
  margin: 10px 20px 10px 10px;
}

.book-info-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.book-title {
  text-decoration: underline;
  margin: 0 10px 0 0;
}

.book-authors {
  color: rgba(0, 0, 0, 0.6);
  margin: 0 10px 0 0;
}

.book-categories {
  background: rgba(140, 0, 100, 0.5);
  border-radius: 20px;
  padding: 0 10px 0 10px;
  display: inline-block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.book-link {
  text-decoration: none;
  color: black;
  background: #f78888;
  padding: 0 10px;
  border: 1px solid black;
  border-radius: 5px;
  text-transform: capitalize;
}

.book-link:hover {
  cursor: pointer;
  background: black;
  color: #f78888;
}

.book-image-link:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.results-navigation {
  font-family: "Baloo Bhai", cursive;
  font-size: 40px;
  display: flex;
  justify-content: center;
}

.max-results-selector {
  font-family: "Baloo Bhai", cursive;
  font-size: 20px;
  margin: 0 10px;
  background: rgba(144, 204, 244, 0.7);
}

.navigation-arrow {
  color: rgba(140, 0, 100);
}

@media screen and (min-width: 690px) {
  .books-container-wrapper {
    margin-top: 100px;
  }

  .books-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
  }
}

@media screen and (min-width: 1300px) {
  .books-container {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.header-wrapper {
  position: fixed;
  width: 100%;
}

.info {
  text-align: center;
  font-size: 26px;
  font-family: "Baloo Bhai", cursive;
  background: #90ccf4;
  margin: 0;
}

.main-wrapper {
  display: flex;
  flex-direction: column;
}

.loading {
  display: flex;
  justify-content: center;
  margin-top: 400px;
  font-size: 70px;
}

@media screen and (min-width: 690px) {
  .info {
    display: none;
  }
  .header-wrapper {
    display: flex;
    background: #ececec;
    justify-content: center;
    align-items: center;
  }
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: #fff49c;
}

